// theme
$color-title: #fff;
$color-sub: #999;
$color-h1: #8c8c8c;
$color-link: #000;
$color-bg: #000;
$font-family: 'Lato','Chosunilbo_myungjo';
$aspect-ratio: 33/17;
$image-width-max: 500px;
// theme



//layout
.container {
    width: 1280px;
    margin: 0 auto;
    /* border: 1px solid var(--color-sub); */
}
#wrap {
    width: 100%;
    background-color: $color-bg;
}
.contents {
    margin: 0 auto;
    width: 100%;
    overflow: hidden;

    section {
        padding: 60vh 0;
    }
}
//layout



//nav
nav {
    position: fixed;
    top: 50%;
    right: 30px;
    transform: translate(0, -50%);
    li {
        margin-top: 20px;
        transition: .2s;

        &.active {
            transform: scale(1.2);

            a .nav-filter {
                opacity: 0;
            }
        }
        :hover {
            transform: scale(1.2);
        }
        a {
            position: relative;
            width: 35px;
            height: 35px;

            .nav-filter {
                position: absolute;
                z-index: 1111;
                top: 0;
                left: 0;
                width: 35px;
                height: 35px;
                background-color: rgba(0, 0, 0, 0.6);
                transition: .5s;
            }
            img {
                display: block;
                width: 35px;
                height: 35px;
            }
            em {
                display: none;
            }
        }
    }
}
//nav



//section0
#section0 {
    h1 {
        font-size: 8vw;
        color: $color-h1;
        text-transform: uppercase;
        padding: 10vw 8vw 0 8vw;
        font-weight: 100;

        em {
            display: block;
            font-family: 'Lato';
            padding-left: 18vw;
            font-weight: normal;
        }
    }
    .moon-main {
        text-align: center;
        width: 80vw;
        display: block;
    }
}
//section0



//section1
#section1 {
    img {
        width: 600px;
        display: inline-block;
    }
    .terrain-wrap{
        float: right;
        width: 500px;

        .terrain-item{
            position: relative;

            &.sea {
                .line {
                    width: 530px;
    
                    .horizon {
                        width: 430px;
                    }
                }
                h2 {
                    margin: 0;
                }
            }
            &.mountain {
                .line {
                    .horizon {
                        width: 330px;
                    }
                }
            }
            &.craters {
                .line {
                    width: 460px;
    
                    .horizon {
                        width: 360px;
                    }
                }
            }
            .line{
                position: absolute;
                height: 100px;
                transform: translate(calc(-100% - 20px), 20px);
                .diagonal{
                    display: inline-block;
                    width: 100px;
                    height: 100%;
                    transition: all .5s;
                    transform: translate(50px, 20.71px) rotate(-45deg);
                    border-top: 1px solid white;
                }
                .horizon{
                    display: inline-block;
                    height: 100px;
                    transition: all .5s;
                    border-top: 1px solid white;
                }
            }
            h2 {
                margin-top: 40px;
                font-size: 40px;
                font-weight: 400;
                color: $color-title;
                transform: translate(-5px, 0);
                transition: all .5s;

                em {
                    font-size: 25px;
                    color: $color-h1;
                }
            }
            p {
                margin-top: 10px;
                padding-left: 10px;
                color: $color-sub;
                line-height: 25px;
                transition: all .5s;
            }
        }
    }
}
//section1



//media query
@media screen and (max-width:1279px) {
    //Desktop
    .container {
        width: 90vw;
    }
    .terrain-wrap {
        margin: 100px auto;
        .terrain-item {
            .line {
                display: none;
            }
        }
    }
}
@media screen and (max-width: 1023px) {
    //Tablet
    #intro h1 {
        font-size: 15vw;
        text-align: center;
    }
    #intro h1 em {
        padding-left: 0;
        font-weight: normal;
    }
    #intro .moon-main {
        width: 100vw;
    }
}
@media screen and (max-width:767px){ 
    //Mobile
    #section1 img {
        width: 80%;
        text-align: center;
    }
    .terrain-wrap {
        float: left;
        width: 80%;
    }
}
//media query
  